function Applicant(params) {

  const [showMore, setShowMore] = useState({})

  const setShowMoreClick = (name, showMoreState) => {
    let copy = showMore
    copy[name] = showMoreState
    setShowMore(copy)
  }

  return (
    <div style={{ borderBottom: '1px solid #E4E4E4', marginBottom: 24 }}>
      <div className='columns is-gapless is-marginless'>
        <div className='column is-1'>
          <Link to={'/candidate/' + params.applicant.id}>
            <figure className="image" style={{ width: 64, height: 64, margin: 'auto' }}>
              <img
                className="is-rounded"
                src={params.applicant.profile_picture}
                alt=''
                style={{ objectFit: 'cover', objectPosition: 'center center', width: '100%', height: '100%' }}
              />
            </figure>
          </Link>
        </div>
        <div className='column is-8'>
          <div>
            <div>
              <Link to={'/candidate/' + params.applicant.id}>
                <h3 style={{ display: 'inline', marginRight: 12 }}>{params.applicant.name}</h3>
              </Link>
              <InlineIconLabel icon='fas fa-map-marker' color='#D1D1D6'>
                {params.applicant.location}
              </InlineIconLabel>
            </div>
            <div>
              {params.applicant.tech_stack && params.applicant.tech_stack.slice(0, 3).map(tech => (
                <div style={{
                  backgroundColor: `${tech.experience === 3 ? '#7F4CDC' : ''}${tech.experience === 2 ? '#A079E5' : ''}${tech.experience === 1 ? '#CCB8F1' : ''}`,
                  display: 'inline-block',
                  color: '#FFFFFF',
                  fontWeight: 'bold',
                  margin: 4,
                  paddingTop: 4,
                  paddingBottom: 4,
                  paddingLeft: 12,
                  paddingRight: 12,
                }}>{tech.name}</div>
              ))}
            </div>
            <div>
              {!showMore[params.applicant.name] && params.applicant.work && params.applicant.work.slice(0, 2).map(work => (
                <div>{work.role} - {work.company.name} | {work.start_date} - {work.end_date}</div>
              ))}

              {!showMore[params.applicant.name] && params.applicant.work && params.applicant.work.length > 2 && <div
                style={{ fontWeight: 'bold', cursor: 'pointer', color: Turquoise }}
                onClick={event => setShowMoreClick(params.applicant.name, !showMore[params.applicant.name])}
              >
                Show {params.applicant.work.length - 2} More Experiences <i className='fas fa-chevron-down' />
              </div>}

              {showMore[params.applicant.name] && params.applicant.work && params.applicant.work.map(work => (
                <div>{work.role} - {work.company.name} | {work.start_date} - {work.end_date}</div>
              ))}

              {showMore[params.applicant.name] && params.applicant.work && params.applicant.work.length > 2 && <div
                style={{ fontWeight: 'bold', cursor: 'pointer', color: Turquoise }}
                onClick={event => setShowMoreClick(params.applicant.name, !showMore[params.applicant.name])}
              >
                Show Less Experiences <i className='fas fa-chevron-up' />
              </div>}
            </div>
          </div>
        </div>
        <div className='column is-3' style={{ textAlign: 'right', marginTop: 16 }}>
          {params.children}
        </div>
      </div>
    </div >
  )
}